.category {
  background-color: #f9f9f9;
  position: relative;
  margin-bottom: 27px;
  overflow: hidden;

  &::after {
    @media (min-width: $width-tablet) {
      position: absolute;
      content: "";
      left: 0;
      right: 0;
      bottom: 0;
      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: repeat-x;
      height: 17px;
      background-size: 768px 24px;
    }

    @media (min-width: $width-desktop) {
      bottom: -12px;
      height: 34px;
      background-size: auto 37px;
      background-position-x: 12px;
    }
  }
}

.category__title-wrapper {
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center 19%;
  min-height: 283px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-repeat: no-repeat;
    background-size: 570px auto;
  }

  @media (min-width: $width-tablet) {
    min-height: 539px;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: center 42%;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-repeat: no-repeat;
      background-size: 1150px auto;
    }
  }

  @media (min-width: $width-desktop) {
    min-height: 551px;
    background-image: url("../img/hero-desktop@1x.jpg");
    background-position: center 40%;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
      background-repeat: no-repeat;
    }
  }
}

.category__title {
  @include text(27px, 30px);
  font-family: $font;
  font-weight: 700;
  color: $black;
  text-align: center;
  margin: 0;
  padding-top: 71px;

  @media (min-width: $width-tablet) {
    @include text(43px, 50px);
    padding-top: 66px;
  }

  @media (min-width: $width-desktop) {
    @include text(53px, 57px);
    padding-top: 78px;
    padding-bottom: 35px;
  }
}

.category__list {
  @include no-list;

  @media (min-width: $width-tablet) {
    display: flex;
    justify-content: center;
  }
}

.category__item {
  @media (min-width: $width-tablet) {
    width: 43.5%;
  }

  @media (min-width: $width-desktop) {
    width: 525px;
  }
}

.category__item--interior {
  background-color: $seafoam;
}

.category__item-link--interior {
  background-color: $seafoam;
  transition: 0.3s;
  position: relative;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
  }

  svg {
    fill: $category-icon;
    opacity: 0.5;
    transition: 0.3s;
  }

  &:hover,
  &:active {
    background-color: #59d5bc;
    transform: translateY(-5px);

    svg {
      opacity: 0.7;
    }
  }

  &:active {
    .category__item-title {
      opacity: 0.3;
    }

    svg {
      opacity: 0.3;
    }
  }
}

.category__item--toy {
  background-color: $grenny;
}

.category__item-link--toy {
  background-color: $grenny;
  transition: 0.3s;
  position: relative;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
  }

  svg {
    fill: $category-icon;
    opacity: 0.5;
    transition: 0.3s;
  }

  &:hover,
  &:active {
    background-color: #46c1ae;
    transform: translateY(-5px);

    svg {
      opacity: 0.7;
    }
  }

  &:active {
    .category__item-title {
      opacity: 0.3;
    }

    svg {
      opacity: 0.3;
    }
  }
}

.category__item-link {
  color: $white;
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 8px 32px 9px 30px;
  position: relative;

  @media (min-width: $width-tablet) {
    padding: 8px 35px 15px 36px;
  }
}

.category__item-title {
  @include text(19px, 24px);
  font-weight: 700;
  color: $white;
  max-width: 120px;

  @media (min-width: $width-tablet) {
    @include text(20px, 30px);
  }
}

.category__item-icon {
  opacity: 0.5;
  margin-left: auto;
}
